

@keyframes rotate_1 {
    from {transform: rotateX(-55deg) skewY(45deg);}
    to {
      transform: rotateX(-55deg) skewY(45deg) rotateZ(360deg);
    }
}
@keyframes rotate_2 {
  0% {transform: rotateX(55deg) skewY(-45deg);}
  to {
    transform: rotateX(55deg) skewY(-45deg) rotateZ(-360deg);
  }
}


@keyframes slide-NavLinks {
    0% {
      opacity: 0;
      transform: translate(-80%,-50%);
    }
    30% {
      opacity: 0;
      transform: translate(-80%,-50%);
    }
    90% {
      opacity: 1;
      transform: translate(-45%,-50%);
    }
    100% {
      opacity: 1;
      transform: translate(-50%,-50%);
    }
}

@keyframes slide-down {
    from {
      opacity: 0;
      transform: translate(-50%,-80%);
    }
    to {
      opacity: 1;
      transform: translate(-50%,-50%);
    }
}